function mockUsers() {

    function getUser(phone) {
        const userStr = localStorage.getItem(phone)
        return userStr ? JSON.parse(userStr) : false
    }

    function setUser(user) {
        localStorage.setItem(user.phone, JSON.stringify(user))
    }

    function successResp(data) {
        return {
            err: 0,
            data
        }
    }

    return {

        login(_user) {
            const { phone, password } = _user

            const user = getUser(phone);

            if (!user) {
                return { err: 2 }
            }

            // 登录成功
            if (user.password === password) {
                return successResp({ token: "TEST_TOKEN-" + user.phone })
            }
            // 密码错误
            return { err: 1 }
        },

        signup(user) {

            if (getUser(user.phone)) {
                return { err: 1 }
            }

            setUser(user)

            return successResp(user)
        },

        getInfo(token) {
            let sp = token.split('-');
            if (sp.length === 2 && getUser(sp[1])) {
                return successResp(getUser(sp[1]))
            }

            return { err: 1 }
        },

        logout(token) {
            return successResp({token})
        }
    }
}

export const localUsers = mockUsers()
