import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    component: Home
  },
  {
    path: '/login',
    name: 'login',
    component: () => import('../views/Login.vue'),
  },
  {
    path: '/signup',
    name: 'signup',
    component: () => import('../views/SignUp.vue'),
  },
  {
    path: '/competitions',
    name: 'competitions',
    component: () => import('../views/Competitions.vue'),
  },
  {
    path: '/competition',
    name: 'competition',
    component: () => import('../views/Competition.vue'),
  },
  {
    path: '/products',
    name: 'products',
    component: () => import('../views/Products.vue'),
  },
  {
    path: '/user',
    name: 'user',
    component: () => import(/* webpackChunkName: "Sign" */ '../views/User.vue'),
  },
  {
    path: '/enroll',
    name: 'enroll',
    component: () => import(/* webpackChunkName: "Sign" */ '../views/Enroll.vue')
  },
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
