import Vue from 'vue';
import { login, signUp, getInfo, logout } from "../api/user"
import { setToken, getToken, removeToken } from '../utils/auth'

const state = Vue.observable({
    username: false,
    userphone: false,
    token: getToken()
});

export const getters = {
    userLogin: () => state.token,
    userName: () => state.username,
    userPhone: () => state.userphone
}

export const mutations = {
    setUser: (user) => {
        state.username = user.name
        state.userphone = user.phone
    },
    setToken: (token) => {
        setToken(token)
        state.token = token
    }
}

export const actions = {
    signup(userInfo) {
        const { name, phone, password } = userInfo
        return new Promise((resolve, reject) => {
            signUp({ name, phone, password }).then(response => {
                const { data } = response
                resolve(data)

            }).catch(error => {
                reject(error)
            })
        })
    },

    login(userInfo) {
        const { phone, password } = userInfo
        return new Promise((resolve, reject) => {
            login({ phone, password }).then(response => {
                const { data } = response
                mutations.setToken(data.token)
                resolve(data)
            }).catch(error => {
                reject(error)
            })
        })
    },

    getInfo(token) {
        return new Promise((resolve, reject) => {
            getInfo(token).then(response => {
                const { data } = response
                mutations.setUser(data)
                resolve(data)
            }).catch(error => {
                reject(error)
            })
        })
    },

    // user logout
    logout() {
        return new Promise((resolve, reject) => {
            logout(state.token).then(() => {
                setToken('')
                removeToken()
                // resetRouter()
                resolve()
            }).catch(error => {
                reject(error)
            })
        })
    },

    resetToken() {
        return new Promise(resolve => {
            setToken('')
            removeToken()
            resolve()
        })
    },
}
